import React, { Component } from "react";
import { connect } from "react-redux";
import { showImagePopup } from "../actions/albums";

class ImagePopup extends Component {
  constructor(props) {
    super(props);
  }

  handleClosePopup = () => {
    this.props.showImagePopup();
  };

  handleDelete = imageToDelete => {
    console.log("imageId in popup delete", imageToDelete);
    this.props.showImagePopup();
  };

  render() {
    return (
      <div style={popupContainer}>
        <button onClick={this.handleClosePopup}>X</button>
        <img style={image} src={this.props.imageToShow} />
      </div>
    );
  }
}

const popupContainer = {
  zIndex: "1",
  overflow: "auto",
  backgroundColor: "rgba(0,0,0,0.4)",
  position: "fixed",
  height: "100%",
  width: "100%",
  left: "0",
  top: "0"
};

const image = {
  maxWidth: "600px",
  maxHeight: "600px",
  padding: "10px",
  backgroundColor: "white"
};

const mapStateToProps = state => {
  console.log("state in popup", state);
  return {
    imageToShow: state.imageToShow
  };
};

export default connect(
  mapStateToProps,
  { showImagePopup }
)(ImagePopup);
